@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1 {
  color: #eeeeee;
  font-family: 'Fredoka One', Helvetica, sans-serif;
  font-style: normal;
}

body {
  background-color: #111111;
  color: #cccccc;
  font-family: "Roboto", Helvetica, sans-serif;
}

.Window-Content {
  padding: 10px;
}

.Project-Data {
  font-family: "Roboto Mono", Courier, monospace;
  font-size: small;
  line-height: 50%;
}

.ToolbarContainer {
  display: flex;
  width: 100%; 
  height: 90px;
}

.ToolbarInner {
  display: flex; 
  flex-direction: column;
  justify-content: center;
  height: 100%;
  margin-left: 15px;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(51,51,51,1);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loadButton {
  display: inline;
  float: none;
}

.statisticsContainer {
  border-radius: 5px;
  background: #cccccc;
  padding: 5px;
}

.statisticBox {
  border-radius: 5px;
  background: #ffffff;
  padding: 5px;

  font-family: "Roboto Mono", Courier, monospace;
  font-size: small;
  color: #111111;
}

.chartWrapper {
 position: relative;
}

.chartWrapper > canvas {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
}

.chartAreaWrapper {
  width: 600px;
  overflow-x: scroll;
}

.dropzoneContainer {
  height: 100%;
}

.dropzone {
  flex: 1 1;
  display: flex;
	cursor:pointer;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 20px;
  border-width: 2px;
  border-radius: 25px;
  height: 100%;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: #111111;
  color: #bdbdbd;
  outline: none;
  transition: 0.3s;
}

.dropzone:hover {
  border-color: rgba(54, 162, 235, 1);
}

.dataTableContainer {
  overflow-x: auto;
}

.dataTable {
  border-collapse: collapse;
  width: 100%;
}

.dataTableRow:hover {
  background-color:#333;
}

.dataTableCell {
  border-bottom: 1px solid #555;
  text-align: left;
  padding: 5px;
}

body .lm_content{
  overflow: scroll;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #222222;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;
}

/* Corner */
::-webkit-scrollbar-corner {
  background: #222222;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

#layoutContainer {
  display: flex;
  flex-direction: column;
  height: 90vh;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: right;
}

.demo-button {
	background-color:transparent;
	border-radius:10px;
	border:1px solid #ffffff;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
  font-family: "Roboto Mono", Courier, monospace;
	font-size:11px;
  padding:3px 8px;
  margin: 5px;
	text-decoration:none;
  transition: 0.3s;
}
.demo-button:hover {
	background-color:transparent;
  font-weight:bold;
  
	border:1px solid rgba(54, 162, 235, 1);
}
.demo-button:active {
	position:relative;
	font-weight:bold;
	top:1px;
	border:1px solid rgba(54, 162, 235, 1);
}

